<template lang="html">

<div class="privado">
<nav class="navbar navbar-dark bg-primary">
  <a href="#" class="navbar-item">ejemplo vue-firebase</a>
</nav>
<div class="container">
  <div class="row ">
    <div class="col-sm-4 mx-auto mt-3">
      <div class="card" style="width: 18rem;">
        <div class="card-body">
          <h5 class="card-title">Agregar</h5>
          <input type="text" placeholder="Autor" v-model="nuevaNota.autor">
          <input type="text" placeholder="Titulo" v-model="nuevaNota.titulo">
          <button type="submit" class="btn btn-primary" v-on:click="add">ADD</button>
        </div>
      </div>
    </div>
  </div>
</div>
<div class="card mx-auto mt-3" style="width: 30rem;">
  <div class="card-header">
    Table in a card
  </div>
  <table class="card-table table">
    <thead>
      <tr>
        <th scope="col">Autor</th>
        <th scope="col">Título</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="nota in notas" v-bind:key="nota.i">
        <td>{{nota.autor}}</td>
        <td>{{nota.titulo}}</td>
      </tr>
      
    </tbody>
  </table>
</div>
<div>
    <div class="mt-3">
      <p>Upload an image to Firebase:</p>
      <input type="file" @change="previewImage">
    </div>
    <div>
      <p>Progress: {{uploadValue.toFixed()+"%"}}
      <progress id="progress" :value="uploadValue" max="100" ></progress>  </p>
    </div>
    <div v-if="imageData!=null">
        <img class="preview" :src="picture">
        <br>
      <button @click="onUpload">Upload</button>
    </div>
  </div>
</div>
</template>

<script lang="js">
import {db} from '../db.js'
import firebase from 'firebase'

  export default  {
    name: 'privado',
    props: [],
    mounted () {

    },
    data () {
      return {
        notas:[],
        nuevaNota:{
          autor:"",
          titulo:""
        },
        imageData: null,
        picture: null,
        uploadValue: 0
      }
    },
    methods: {
      add:function(){
        db.collection("listaNotas").add(this.nuevaNota);
        this.nuevaNota.autor="";
        this.nuevaNota.titulo="";
      },
      previewImage(event) {
        this.uploadValue=0;
        this.picture=null;
        this.imageData = event.target.files[0];
      },

      onUpload(){
        this.picture=null;
        const storageRef=firebase.storage().ref(`${this.imageData.name}`).put(this.imageData);
        storageRef.on(`state_changed`,snapshot=>{
          this.uploadValue = (snapshot.bytesTransferred/snapshot.totalBytes)*100;
        }, error=>{console.log(error.message)},
        ()=>{this.uploadValue=100;
          storageRef.snapshot.ref.getDownloadURL().then((url)=>{
            this.picture =url;
          });
        }
        );
      }
      
    },
    computed: {

    },
    firestore: {
      notas:db.collection("listaNotas"),
    }
}


</script>

<style scoped>
  .privado {

  }
</style>
