<template lang="html">

  <section class="registro">
    <div style="width:20rem" class="bg-light mx-auto p-4">
    <h3> Regístrate </h3>
  <div class="form-group">
    <label for="exampleInputEmail1">Correo Electrónico</label>
    <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" v-model="usuario.email">
  </div>
  <div class="form-group">
    <label for="exampleInputPassword1">Contraseña</label>
    <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Password" v-model="usuario.contr">
  </div>
  <br>
  <button type="submit" class="btn btn-primary" v-on:click="reg()">Registrarme</button>
</div>
  </section>
  
</template>

<script lang="js">
import firebase from 'firebase'


  export default  {
    name: 'registro',
    props: [],
    mounted () {

    },
    data () {
      return {
        usuario:{
          email:"",
          contr:""
        }
      }
    },
    methods: {
      reg(){
        firebase.auth().createUserWithEmailAndPassword(this.usuario.email, this.usuario.contr).then(() => {
          this.$router.replace('/login');
        }).catch(function(error) {
          var errorMessage = error.message;
          alert(errorMessage)
          });
        }
    },
    computed: {

    }
}


</script>

<style scoped>
  .registro {

  }
</style>
