<template lang="html">

  <section class="login">
<div style="width:20rem" class="bg-light mx-auto p-4">
  <div class="form-group">
    <label for="exampleInputEmail1">Correo Electrónico</label>
    <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" v-model="usuario.email">
  </div>
  <div class="form-group">
    <label for="exampleInputPassword1">Contraseña</label>
    <input type="password" class="form-control" id="exampleInputPassword1" placeholder="Password" v-model="usuario.contr">
  </div>
  <br>
  <button type="submit" class="btn btn-primary mx-3" v-on:click="login()">Entrar</button>
  <button type="submit" class="btn btn-primary" v-on:click="regist()">Registrarme</button>
  <button type="submit" class="btn btn-primary mt-3" v-on:click="loginFace()">Entrar con Facebook</button>
</div>
  </section>

</template>

<script lang="js">
import firebase from 'firebase'


  export default  {
    name: 'login',
    props: [],
    mounted () {

    },
    data () {
      return {
        usuario:{
          email:"",
          contr:""
        }
      }
    },
    methods: {
      login(){
        firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.contr).then(() => {
          this.$router.replace('/privado');
        }).catch((err) => {
          alert(err.message)
        });
      },
      regist(){
          this.$router.replace('/registro');
      },
      loginFace(){
        var provider = new firebase.auth.FacebookAuthProvider();
        firebase.auth().signInWithPopup(provider).then(()=> {
          this.$router.replace('/privado');
        }).catch((error)=>{
          var errorMessage = error.message;
          alert(errorMessage);
        });
      }
    },
    computed: {

    }
}


</script>

<style scoped>
  .login {

  }
</style>
